
import { store } from "@/store";
import { onMounted, onUnmounted, PropType, ref } from "vue";

import { useRoute, useRouter } from "vue-router";

type galleryContent = { preview: string; full: string; ratio: number };
type galleryType = "all" | "advertisement" | "faces" | "dance" | "nudes" | "fashion";
type currentGalley = { type: galleryType; image: galleryContent }[];

export default {
  props: {
    currentGalleryList: Array as PropType<Array<{ type: galleryType; image: galleryContent }>>,
  },
  components: {},
  setup(props) {
    const route = useRoute();
    const router = useRouter();

    const currentImage = ref(
      props.currentGalleryList![store.state.portfolioView.currentGalleryIndex]
    );
    // const currentImageSrc = ref(currentImage.value.image.preview);

    const imageDirection = ref<"left" | "right">("right");

    const loadedImageState = ref<"preview" | "full">("preview");

    processImage();
    function processImage() {
      currentImage.value = props.currentGalleryList![store.state.portfolioView.currentGalleryIndex];
      loadedImageState.value = "preview";
      const image = new Image();
      image.src = currentImage.value.image.full;
      image.onload = () => {
        // function onImageLoadFull() {
        loadedImageState.value = "full";
        // window.alert(loadedImageState.value);
        // currentImageSrc.value = currentImage.value.image.full;
        // const elementa = document.getElementById("image-transition-wrapper")?.DOCUMENT_TYPE_NODE;
        // window.alert(
        //   loadedImageState.value !== "full"
        //     ? props.currentGalleryList![store.state.portfolioView.currentGalleryIndex].image.preview
        //     : props.currentGalleryList![store.state.portfolioView.currentGalleryIndex].image.full
        // );
      };
    }

    function nextImage() {
      if (store.state.portfolioView.currentGalleryIndex >= props.currentGalleryList!.length - 1) {
        store.state.portfolioView.currentGalleryIndex = 0;
        // return;
      } else {
        store.state.portfolioView.currentGalleryIndex++;
      }
      imageDirection.value = "right";
      router.push({
        name: route.name!,
        query: { ...route.query, full: "true" },
      });
      processImage();
    }
    function previousImage() {
      if (store.state.portfolioView.currentGalleryIndex <= 0) {
        store.state.portfolioView.currentGalleryIndex = props.currentGalleryList!.length - 1;
        // return;
      } else {
        store.state.portfolioView.currentGalleryIndex--;
      }
      imageDirection.value = "left";
      router.push({
        name: route.name!,
        query: { ...route.query, full: "true" },
      });
      processImage();
    }
    function closeImage() {
      let queryParams = JSON.parse(JSON.stringify(route.query));
      //   console.log(queryParams);
      if ("full" in queryParams) {
        delete queryParams.full;
      }
      router.push({
        name: route.name!,
        query: queryParams,
      });
      store.state.portfolioView.currentGalleryIndex = -1;
      //   console.log(queryParams);
      // router.replace({ query: queryParams });
    }

    window.addEventListener("keydown", handleKeys);

    onUnmounted(() => {
      window.removeEventListener("keydown", handleKeys);
    });

    function handleKeys(event: KeyboardEvent) {
      //   console.log(event);

      if (event.key === "ArrowLeft") {
        previousImage();
      }
      if (event.key === "ArrowRight") {
        nextImage();
      }
      if (event.key === "Escape") {
        closeImage();
      }

      if (event.key === "ArrowUp" || event.key === "ArrowDown" || event.key === "Tab") {
        event.preventDefault();
      }
    }

    onMounted(() => {
      const routeIndexParam = route.query.index;
      if (routeIndexParam) {
        store.state.portfolioView.currentGalleryIndex = Number(routeIndexParam);
        store.state.navigationBar.isVisible = true;
        store.state.navigationBar.transparent = false;
      }

      document.addEventListener("touchstart", handleTouchStart, false);
      // document.addEventListener("touchmove", handleTouchMove, false);
      document.addEventListener("touchend", handleTouchEnd, false);
      document.addEventListener("touchcancel", handleTouchEnd, false);
    });

    onUnmounted(() => {
      document.removeEventListener("touchstart", handleTouchStart, false);
      // document.addEventListener("touchmove", handleTouchMove, false);
      document.removeEventListener("touchend", handleTouchEnd, false);
      document.removeEventListener("touchcancel", handleTouchEnd, false);
    });

    const touchStart = { x: -1, y: -1 };
    function handleTouchStart(this: Document, ev: TouchEvent) {
      touchStart.x = ev.touches.item(0)!.clientX;
      touchStart.y = ev.touches.item(0)!.clientY;
    }
    function handleTouchEnd(this: Document, ev: TouchEvent) {
      // console.log(ev);

      const touchEnd = {
        x: ev.changedTouches.item(0)!.clientX,
        y: ev.changedTouches.item(0)!.clientY,
      };
      const touchLength = Math.sqrt((touchEnd.x - touchStart.x) ** 2);
      // console.log(touchLength);
      // console.log(touchStart);
      // console.log(touchEnd);
      if (touchLength < 50) {
        return;
      }
      if (touchEnd.x < touchStart.x) {
        //swipe left
        nextImage();
      } else {
        previousImage();
      }
    }

    return {
      imageDirection,
      store,
      loadedImageState,
      functions: {
        nextImage,
        previousImage,
        closeImage,
      },
    };
  },
};
