
import { ref, onMounted, Ref } from "vue";
// import diashow from "@/components/diashow.vue";
import circleLoader from "@/components/circleLoader.vue";

// import { useStore } from 'vuex';
// import { key } from '@/store';

import { store } from "@/store";

import { useRoute, useRouter } from "vue-router";

import { clickOnLink } from "@/functions/link-without-redirect";

export default {
  components: {
    // diashow,
    circleLoader,
  },
  setup() {
    // const store = theStore;
    const route = useRoute();
    const router = useRouter();

    const routeComponent = ref<HTMLElement | null>(null);
    const routeWrapperEl = ref<HTMLElement | null>(null);

    let scrollElement: HTMLElement | Window = window;

    //100vh on mobile
    addEventListener("resize", checkResize);
    function checkResize() {
      try {
        scrollElement.removeEventListener("scroll", hanldeScroll);
      } catch (error) {}

      //mobile
      if (window.innerWidth <= 960) {
        scrollElement = window;
        store.state.navigationBar.isMobile = true;
        // console.log("add to window");
      } else {
        //desktop
        // console.log("add to wrapper");
        store.state.navigationBar.isMobile = false;
        scrollElement = routeWrapperEl.value!;
        // scrollElement = document.getElementById("page-wrapper")!;
      }
      store.state.navigationBar.scrollElement = scrollElement;
      // console.log(routeWrapperEl.value);
      // console.log(window);
      // console.log(scrollElement);
      // console.log(window.innerWidth);
      // console.log(window.innerWidth <= 960);
      scrollElement.addEventListener("scroll", hanldeScroll);

      if (store.state.currentPage === "home" && getInitialHeight === true) {
        return;
      }
      getInitialHeight = true;
      const height100 = window.innerHeight;
      console.log(window.innerHeight);
      document.documentElement.style.setProperty("--100vh", `${height100}px`);
      // document.documentElement.style.setProperty("--100vh", `${height100}px`);
    }

    let getInitialHeight = false;

    //scroll prox
    const useScrollProx = ref(false);

    //get current browser
    let userAgent = navigator.userAgent;
    console.log(userAgent);
    if (userAgent.match(/chrome|chromium|crios/i)) {
      store.state.currentBrowser = "chrome";
    } else if (userAgent.match(/firefox|fxios/i)) {
      store.state.currentBrowser = "firefox";
    } else if (userAgent.match(/safari/i)) {
      store.state.currentBrowser = "safari";
    } else if (userAgent.match(/opr\//i)) {
      store.state.currentBrowser = "opera";
    } else if (userAgent.match(/edg/i)) {
      store.state.currentBrowser = "edge";
    } else {
      store.state.currentBrowser = "other";
    }
    // console.log(store.state.currentBrowser);

    let homeShowRegularLogo = ref(false);
    let timerId: null | number = null;

    function hanldeScroll() {
      // const scrollElements: NodeListOf<Element> = document.querySelectorAll("body")!;
      // let scrollElement = scrollElements[0];
      // if (scrollElements.length > 1) {
      //   scrollElement = scrollElements[scrollElements.length - 1];
      // }
      // console.log("a");
      // if (!scrollElement) {
      //   return;
      // }
      // scrollElement.scroll({ top: 0, behavior: "smooth" });

      // console.log("scrollas");
      let scrollYAmount;

      if (scrollElement == window) {
        scrollYAmount = scrollElement.scrollY;
        // console.log("from window");
      } else {
        scrollYAmount = (scrollElement as HTMLElement).scrollTop;
        // console.log("from el");
      }
      // console.log(scrollYAmount);
      if (scrollYAmount <= 0) {
        //set navbar transparent
        // navbarTransparent.value = true;
        if (route.name !== "portfolio") {
          store.state.navigationBar.transparent = true;
        }
        homeShowRegularLogo.value = false;
        if (timerId !== null) {
          clearTimeout(timerId);
        }
      } else {
        store.state.navigationBar.transparent = false;
        // navbarTransparent.value = false;

        if (timerId === null) {
          timerId = setTimeout(() => {
            homeShowRegularLogo.value = true;
          }, 0.5);
        }
      }
    }

    const isHomePage = ref(false);
    router.afterEach((to, from) => {
      // navbarShown.value = true;
      store.state.navigationBar.isVisible = true;
      // store.state.navigationBar.transparent = true;

      store.state.currentPage = String(to.name);
      // to and from are both route objects.
      closeNavbar();

      if (from.name === to.name) {
        // const scrollElements: NodeListOf<Element> = document.querySelectorAll("body")!;
        // let scrollElement = scrollElements[0];
        if (to.name !== "portfolio") {
          scrollElement.scroll({ top: 0, behavior: "smooth" });
        }
      }

      if (to.name == "home") {
        document.documentElement.style.setProperty("--perspective", `8px`);
        document.documentElement.style.setProperty("--transformStyle", `preserve-3d`);
        // console.log(from.name);
        if (from.name !== "home") {
          // console.log("gere");
          store.state.navigationBar.transparent = true;
        }

        useScrollProx.value = true;
        isHomePage.value = true;
        // if (from.name) {
        // navbarTransparent.value = true;
        // }
      } else {
        document.documentElement.style.setProperty("--perspective", `none`);
        document.documentElement.style.setProperty("--transformStyle", `none`);
        store.state.navigationBar.transparent = true;

        if (from.name !== to.name) {
        }
        useScrollProx.value = false;
        isHomePage.value = false;
      }

      if (to.name === "portfolio") {
        store.state.navigationBar.transparent = false;
        if (to.query.full) {
          document.documentElement.style.setProperty("--body-overflow", `hidden`);

          // store.state.portfolioView.currentGalleryIndex = Number(to.query.index);
        } else {
          document.documentElement.style.setProperty("--body-overflow", `auto`);
          store.state.portfolioView.currentGalleryIndex = -1;
        }
        // if (to.query.index) {
        //   document.documentElement.style.setProperty("--body-overflow", `hidden`);
        //   store.state.portfolioView.currentGalleryIndex = Number(to.query.index);
        // } else if (!to.query.index && from.query.index) {
        //   document.documentElement.style.setProperty("--body-overflow", `auto`);
        //   store.state.portfolioView.currentGalleryIndex = -1;
        // }
      } else {
        // document.documentElement.style.setProperty("--perspective", `8px`);
        // document.documentElement.style.setProperty("--transformStyle", `preserve-3d`);
        document.documentElement.style.setProperty("--body-overflow", `auto`);
      }
    });

    function routeEnter() {
      // console.log('enter');
      scrollElement.scroll({ top: 0, behavior: "auto" });
      checkResize();

      hanldeScroll();
      hanldeScroll();
      try {
        scrollElement.removeEventListener("scroll", hanldeScroll);
      } catch (error) {}
      scrollElement.addEventListener("scroll", hanldeScroll);
    }

    let navbarState: Ref<"closed" | "open"> = ref("closed");

    const navbarTransparent = ref(true);
    const navbarShown = ref(true);

    const showModal = ref(false);

    const loaderTimings = {
      instantLoadingTimer: 0.1,
      minDisplatTime: 1,
      maxDisplayTime: 10,
      currentDisplayTime: 0,
      isLoaded: false,
      showLoader: ref(true),
    };
    const loadingTimer = setInterval(() => {
      loaderTimings.currentDisplayTime += 0.1;
      if (
        loaderTimings.isLoaded &&
        loaderTimings.minDisplatTime <= loaderTimings.currentDisplayTime
      ) {
        loaderTimings.showLoader.value = false;
        clearInterval(loadingTimer);
      }
    }, 100);

    //hide on scroll
    let prevScrollpos = window.pageYOffset;
    onMounted(() => {
      window.addEventListener("load", function (event) {
        loaderTimings.isLoaded = true;
      });
    });

    function closeNavbar(clickLogo = false) {
      if (clickLogo === true) {
        //check if already on home screen, then just scroll up

        return;
      }
      navbarState.value = "closed";
    }

    function toggleNavbar() {
      if (navbarState.value === "closed") {
        navbarState.value = "open";
      } else {
        navbarState.value = "closed";
      }
    }

    return {
      isHomePage,
      routeEnter,
      routeWrapperEl,
      routeComponent,
      store,
      navbarState,
      showLoader: loaderTimings.showLoader,
      showModal,
      useScrollProx,
      navBar: {
        prevScrollpos,
        navbarShown,
        navbarTransparent,
      },
      functions: {
        toggleNavbar,
        clickOnLink,
      },
    };
  },
};
